  background-color: var(--stepper-button-disabled-color, #f7f8fa);
}
.van-stepper__minus {
  border-radius: 0.17067rem 0 0 0.17067rem;
  border-radius: var(--stepper-border-radius, 0.17067rem) 0 0 var(--stepper-border-radius, 0.17067rem);
}
.van-stepper__minus::after {
  display: none;
}
.van-stepper__plus {
  border-radius: 0 0.17067rem 0.17067rem 0;
  border-radius: 0 var(--stepper-border-radius, 0.17067rem) var(--stepper-border-radius, 0.17067rem) 0;
}
.van-stepper .van-stepper__input .weui-input {
  height: 100%;
}
.van-stepper--round .van-stepper__input {
  background-color: transparent !important;
}
.van-stepper--round .van-stepper__plus,
.van-stepper--round .van-stepper__minus {
  border-radius: 100%;
}
.van-stepper--round .van-stepper__plus:active,
.van-stepper--round .van-stepper__minus:active {
  opacity: 0.7;
}
.van-stepper--round .van-stepper__plus--disabled,
.van-stepper--round .van-stepper__minus--disabled,
.van-stepper--round .van-stepper__plus--disabled:active,
.van-stepper--round .van-stepper__minus--disabled:active {
  opacity: 0.3;
}
.van-stepper--round .van-stepper__plus {
  color: #fff;
  background-color: #ee0a24;
}
.van-stepper--round .van-stepper__minus {
  color: #ee0a24;
  background-color: #fff;
  border: 0.04267rem solid #ee0a24;
}
.van-stepper__input {
  display: inline-block;
  box-sizing: border-box;
  min-height: 0;
  margin: 0.04267rem;
  padding: 0.04267rem;
  text-align: center;
  vertical-align: middle;
  border: 0;
  border-width: 0.04267rem 0;
  border-radius: 0;
  -webkit-appearance: none;
  font-size: 0.59733rem;
  font-size: var(--stepper-input-font-size, 0.59733rem);
  color: #323233;
  color: var(--stepper-input-text-color, #323233);
  background-color: #f2f3f5;
